import { observer } from 'mobx-react-lite';
import React from 'react'
import { Link } from 'react-router-dom'
import { Container, Header, Segment,Image, Button } from 'semantic-ui-react'
import { useStore } from '../../app/stores/store';
import LoginForm from '../users/LoginForm';
import RegsiterForm from '../users/RegisterForm';

const HomePage = () => {

  const {userStore, modalStore} = useStore();

  return (
    <Segment inverted textAlign='center' vertical className='masthead' >
        <Container text >
            <Header as='h1' inverted>
              <Image size='massive' src='/assets/logo.png'  alt='logo' style={{marginBottom:'12px'}}/>
              Reactivities
            </Header>
            {userStore.isLoggedIn 
              ? (
                  <>
                    <Header as='h2' inverted content='Welcome to Reactivities' />
                    <Button as={Link} to='/activities' size='huge' inverted >
                      Go to Activities!
                    </Button>
                  </>)
              : (
                <>
                  <Button  size='huge' inverted onClick={() => modalStore.openModal(<LoginForm/>)} >
                    Login!
                  </Button>
                  <Button  size='huge' inverted onClick={() => modalStore.openModal(<RegsiterForm />)} >
                    Register!
                  </Button>
                </>
                )
            }
            
        </Container>
    </Segment>
  )
}

export default observer(HomePage) 
